import React from 'react';
import { connect } from 'react-redux';

interface Props {
  showModalForm: () => void;
}

function TodoFooter(props: Props) {
  const { showModalForm } = props;
  const hdlClackAdd = () => {
    showModalForm();
  };

  return (
    <div className="todos-footer">
      <button className="button" onClick={hdlClackAdd} type="button">
        Add todos
      </button>
    </div>
  );
}

interface Dispatch {
  (type: { type: string }): void;
}

const mapDispatchToProps = (dispatch: Dispatch) => ({
  showModalForm: () => dispatch({ type: 'SHOW_MODAL' }),
});

export default connect(null, mapDispatchToProps)(TodoFooter);
