import React, { useState } from 'react';
import { connect } from 'react-redux';
import { StateInit } from '../../store/reducer';
import TodoFooter from '../../components/TodoFooter/TodoFooter';

interface Props {
  state: StateInit;
  selectCategory: (id: number) => void;
}

function TodoCategories(props: Props) {
  const { state, selectCategory } = props;
  const [isShow, setVisibility] = useState(false);

  const curCatId = state.currentCategoryId;
  const init = {
    categoryes: [],
  };
  const categoryClick = (id: number) => {
    selectCategory(id);
  };
  const showClick = () => {
  	setVisibility(!isShow);
  }

  const keyDown = () => {};
  return (
    <>
      <div className="todo-title">
        <h3> Todo categories </h3>
      </div>
      <div 
      	className={`back-font ${(isShow)? 'fadein': 'fadeout'}`}
      	onClick={showClick}>
      </div>
      <div className="todo-button" onClick={showClick}></div>
      <div className={`todos-container ${(isShow)? 'show': 'hide'}`}>
      	<div className="todos-items">
	        {state
	          ? state.categoryes.map((item, idx) => (
	              <div
	                role="button"
	                tabIndex={idx}
	                onKeyDown={keyDown}
	                className={`todos-card ${(curCatId === item.id)? 'active' : ''}`}
	                onClick={() => categoryClick(item.id)}
	                key={item.description}
	              >
	                {item.description}
	                <p>{item.createdAt}</p>
	              </div>
	            ))
	          : ''}
          </div>
        <TodoFooter />
      </div>
    </>
  );
}
interface Dispatch {
  (type: { type: string; payload: number }): void;
}

const mapStateToProps = (state: StateInit) => ({ state });
const mapDispatchToProps = (dispatch: Dispatch) => {
  return {
    selectCategory: (id: number) =>
      dispatch({ type: 'SELECT_CATEGORY', payload: id }),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TodoCategories);
