import React from 'react';
import { TasksList } from '../../store/reducer';

interface Props {
  tasksList: Array<TasksList>;
}

function TasksTable(props: Props) {
  const { tasksList } = props;
  const isDone = '';

  return (
    <div className="tasks-table">
      {tasksList
        ? tasksList.map((i, idx) => (
            <div
              className={`task-item ${i.isCompleted ? 'done' : ''}`}
              key={i.id}
            >
              <p className="task-title">{i.titleDescription}</p>
              <p>Last modified on: {i.createdAt}</p>
            </div>
          ))
        : ''}
    </div>
  );
}

export default TasksTable;
