export interface TasksList {
  id: number;
  titleDescription: string;
  isCompleted: boolean;
  createdAt: string;
}

export interface Categoryes {
  id: number;
  description: string;
  createdAt: string;
  tasksList: Array<TasksList>;
}

export interface StateInit {
  currentCategoryId: number;
  currentTaskId: number;
  isVisibleModal: boolean;
  categoryes: Array<Categoryes>;
}

const reducer = function (state: StateInit = initState, action: Actions) {
  const newState = { ...state };
  switch (action.type) {
    case 'ADD_CATEGORY':
      {
        const lastCId:number = newState.categoryes.length - 1;
        const newID:number = newState.categoryes[lastCId].id + 1;
        const title = (typeof action.payload === 'object')
          ? action.payload[0].value 
          : `New Category ${newID}`;
        const newCategory = {
          id: newID,
          description: title,
          createdAt: new Date().toLocaleString('en-US'),
          tasksList: [],
        };
        newState.categoryes.push(newCategory);
        newState.isVisibleModal = true;
        newState.currentCategoryId = newID;
      }
      break;
    case 'SELECT_CATEGORY': {
      if (typeof action.payload === 'number')
        newState.currentCategoryId = action.payload;
        // newState.isVisibleModal = true;
    }
    break;
    case 'REMOVE_MODAL':{
      newState.isVisibleModal = false;
    }
    break;
    case 'SHOW_MODAL':{
      newState.isVisibleModal = true;
    }
  }

  return newState;
};

export { reducer };
